* {
    margin: 0;
    padding: 0;
    font-family: NanumGothic;
}
body,html,#app {
    height: 100%;
}
ul,li {
    list-style: none;
}
section {
    height: 100%;
}
/* nav */
.menu_box {
    width: 100%;
    height: 50px;
    position: fixed;
    transition:all 0.5s ease-in-out;
    -webkit-transition:all 0.5s ease-in-out;
    z-index: 99;
}
.menu_inner {
    width: 63%;
    height:50px;
    margin:0 auto;
}
.fixed {
    background-color: #1B242F;
    border-bottom: 3px solid #FFE05C;
    transition:all 0.5s ease-in-out;
    -webkit-transition:all 0.5s ease-in-out;
}
/* .menu_box .menu_inner > ul {
    float : left;
    padding: 0 35px 0 0;
} */
/* .menu_inner li {
    float: left;
    text-align: center;
    margin-left: 50px;
} */
.is-active {
    color : #E31B6D !important;
}
.menu_inner a {
    text-decoration: none;
    color : #fff;
    line-height: 50px;
    font-size: 17px;
    display: inline-block;
    margin-left: 50px;
}
.menu_inner a:hover {
    color : #E31B6D;
    -webkit-transition:all 0.5s ease-in-out;
    transition:all 0.5s ease-in-out;
}
nav a#trigger {
    display: none;
}
/* common */
.flex {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    text-align: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.header {
    font-size: 30pt;
    position: relative;
}
/* .waypoint {
    opacity: 0;
} */
.header-bar {
    width : 100px;
    height: 4px;
    background: #000;
    margin : 0 auto;
}
.inner {
    width: 63%;
    margin : 0 auto;
    padding: 80px 0;
}
/*animation*/
@-webkit-keyframes slideInLeft {
from {
        opacity: 0;
        -webkit-transform: translateX(-300px);
                transform: translateX(-300px);
}
to {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
}
}
@keyframes slideInLeft {
from {
        opacity: 0;
        -webkit-transform: translateX(-300px);
                transform: translateX(-300px);
}
to {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
}
}
@-webkit-keyframes slideInRight {
from {
      opacity: 0;
      -webkit-transform: translateX(300px);
              transform: translateX(300px);
}
to {
      opacity: 1;
      -webkit-transform: translateX(0);
              transform: translateX(0);
}
}
@keyframes slideInRight {
from {
      opacity: 0;
      -webkit-transform: translateX(300px);
              transform: translateX(300px);
}
to {
      opacity: 1;
      -webkit-transform: translateX(0);
              transform: translateX(0);
}
}
@-webkit-keyframes slideUp {
from {
        -webkit-transform: translateY(300px);
                transform: translateY(300px);
}
to {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
}
}
@keyframes slideUp {
from {
        -webkit-transform: translateY(300px);
                transform: translateY(300px);
}
to {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
}
}
@-webkit-keyframes slideUpBtn {
from {
        -webkit-transform: translateY(100px);
                transform: translateY(100px);
}
to {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
}
}
@keyframes slideUpBtn {
from {
        -webkit-transform: translateY(100px);
                transform: translateY(100px);
}
to {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
}
}
.slideInRight {
  -webkit-animation: slideInRight .75s ease both;
          animation: slideInRight .75s ease both;
}
.slideInLeft {
    -webkit-animation: slideInLeft .75s ease both;
            animation: slideInLeft .75s ease both;
}
.slideUp {
    -webkit-animation: slideUp .75s ease both;
            animation: slideUp .75s ease both;
}
/* home */
#home {
    width:100%;
    height:100%;
    background-size: cover;
    -webkit-box-shadow: inset 0 0 0 1000px rgba(0,0,0,.6);
