    border : 2px solid #fff;
    text-align: center;
    text-decoration: none;
    color : white;
    font-size: 16pt;
    -webkit-transition:all 0.3s;
    transition:all 0.3s;
}
.view-btn:hover {
    background-color: #04C2C9;
    -webkit-transition:all 0.3s;
    transition:all 0.3s;
    border : 2px solid #04C2C9;
}
.view-btn > i {
    padding-left: 10px;
    top: 5px;
}
/* about */
#about { width:100%; height:100%; background:#f5f5f5;
}
.chart {
    margin: 100px 20px;
    width: 100%;
}
#portfolio { width:100%;background:#f5f5f5;
}
/* contact */
